import React, { useEffect, useState } from 'react';
import axios from 'axios';
import style from '../../static/style.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Form,
    FormGroup,
    Input,
    Label

} from "reactstrap";

function MovieAdd(){

    const[movie, setMovie] = useState([])

    useEffect(() => {
        axios({
            method: "POST",
            dataType: "jsonp",
            url: "http://127.0.0.1:8000/poster/movies/"
        }).then(response => {
            setMovie(response.data)
        })
    }, [])

  return (
   <div>
   <div className="choose_bar">
                <div className="container">
                    <Link to={{pathname: `/movies/`, fromDashboard: false }} >Афиша</Link>
                    <Link to={{pathname: `/about/`, fromDashboard: false }}>О нас</Link>
                    <Link to={{pathname: `/contact/`, fromDashboard: false }}>Контакты</Link>
                    <Link href="#">Войти</Link>
                </div>
     </div>

     <div className="container">
        <Form>
            <Form.Item  label="Название">
              <Input placeholder="Название" name='name' />
            </Form.Item>

            <Form.Item label="Тип">
              <Input placeholder="Тип" name='type' />
            </Form.Item>

             <Form.Item label="Год">
              <Input placeholder="Год" name='year' />
            </Form.Item>

            <Form.Item label="Актеры">
              <Input placeholder="Тип" name='actor' />
            </Form.Item>

             <Form.Item >
              <Button type="primary" htmlType="submit">Submit</Button>
            </Form.Item>
          </Form>
     </div>

   </div>

  );
}

export default MovieAdd;
